<template>
  <div>
    <group>
      <calendar v-model="demo1" :title="$t('Basic Usage')" disable-past></calendar>
    </group>
    <group>
      <calendar v-model="demo2" :title="$t('set value as TODAY')" disable-past></calendar>
    </group>
    <group>
      <calendar v-model="demo3" :title="$t('disable future')" disable-future></calendar>
    </group>
  </div>
</template>

<i18n>
Basic Usage:
  zh-CN: 
set value as TODAY:
  zh-CN: 
disable future:
  zh-CN: 
</i18n>

<script>
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
  components: {
    Calendar,
    Group,
    Cell
  },
  data () {
    return {
      demo1: '',
      demo2: 'TODAY',
      demo3: 'TODAY'
    }
  }
}
</script>



